@import url(https://fonts.googleapis.com/css?family=Open + Sans);
.nav-menu {
  display: flex;
  justify-content: space-between;
  list-style: none;
  display: inline-block;
}

.nav-menu:first-child {
  margin-right: 50px;
}

.Navigation-link {
  color: black;
  text-decoration: none;
  font-size: 20px;
}

.movie-link {
  display: block;
  cursor: pointer;
  margin-bottom: 15px;
  color: black;
}

h1 {
  margin-left: 20px;
  text-align: center;
}

body {
  background: #f2f2f2;
  font-family: "Open Sans", sans-serif;
}

.search {
  width: 600px;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 600px;
  border: 3px solid #eee;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: black;
}

.searchTerm:focus {
  color: black;
}

.searchButton {
  width: 40px;
  height: 36px;
  border: 1px solid #eee;
  background: grey;
  text-align: center;
  color: black;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.wrap {
  width: 45%;
  position: absolute;
  top: 13%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
}

.movie-list {
  margin-top: 100px;
}

.review-list {
  list-style: none;
  font-size: 20px;
  color: black;
}

.main {
  width: 100vw;
  height: 100vh;
}
.bg-img {
  height: 100%;
}

.bg-img .poster {
  height: 80%;
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-8%, -50%);
  box-shadow: 1px 1px 19px 0px rgba(0, 0, 0, 0.67);
}
.back {
  color: black;
  position: absolute;
  top: 50%;
  left: 15%;
  transform: translate(-50%, -50%);
}
.text {
  height: 100%;
  position: relative;
}
.next {
  color: black;
  position: absolute;
  top: 50%;
  right: 5%;
  transform: translate(-50%, -50%);
}
.search {
  width: auto;
  float: right;
  border: 2px solid #bdbdbd;
  border-radius: 20px;
  color: black;
  padding: 5px 10px;
  margin: 30px 40px;
}
.search input {
  border: 0;
  text-align: right;
  outline: none;
}
.containt {
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 90%;
  width: 90%;
  color: black;
  padding: 40px 20px 40px 100px;
}
.containt h2 {
  color: black;
  padding-top: 10px;
  padding-bottom: 20px;
  font-weight: 500;
  font-size: 30px;
}
.containt h3 {
  font-weight: 300;
  font-size: 25px;
}
.containt h4 {
  color: black;
  font-weight: 400;
  margin-top: 25px;
  margin-bottom: 25px;
  line-height: 36px;
  letter-spacing: 2px;
  font-size: 18px;
}
.containt .btn-cont {
  padding: 30px 0;
}
.mybtn {
  float: left;

  padding: 5px 25px;
  color: black;
  border: 2px solid #bdbdbd;
  border-radius: 50px;
  background-color: #fff;
  font-size: 20px;
  outline: 0;
}
.mybtn:hover,
.mybtn:active {
  color: black;
  background-color: #bdbdbd;
}
.btn-cont span {
  padding-left: 29px;
  padding-top: 5px;
  display: inline-block;
}
.btn-cont span ul {
  display: inline-block;
  font-size: 20px;
}
.btn-cont span ul li {
  list-style-type: circle;
  float: left;
  margin: 0 10px;
}

.additional {
  color: black;
}

.additional:link {
  color: black;
  font-size: 18px;
}
.additional:visited {
  color: black;
}

.additional:active {
  color: red;
}

.review-author {
  font-weight: bold;
}

